import React, { Suspense, lazy } from 'react';

import './App.css';

import { HashRouter, Routes, Route } from 'react-router-dom';
import MySideNav from './components/MySideNav';

const Home = lazy(() => import('./pages/Home'));

const ZHome = lazy(() => import('./pages/zTable/ZHome'));
const ZFromP = lazy(() => import('./pages/zTable/ZFromP'));
const PFromZ = lazy(() => import('./pages/zTable/PFromZ'));

const THome = lazy(() => import('./pages/tTable/THome'));
const TFromP = lazy(() => import('./pages/tTable/TFromP'));
const PFromT = lazy(() => import('./pages/tTable/PFromT'));
const WelchsDf = lazy(() => import('./pages/tTable/WelchsDf'));

const ChiSqHome = lazy(() => import('./pages/chiSqTable/ChiSqHome'));
const ChiSqFromP = lazy(() => import('./pages/chiSqTable/ChiSqFromP'));
const PFromChiSq = lazy(() => import('./pages/chiSqTable/PFromChiSq'));

function App() {
  return (
    <HashRouter>
      <Suspense fallback={<div>Loading...</div>}>
        <MySideNav />
        <Routes>
          <Route path="/" element={<Home />} />

          <Route path="/z" element={<ZHome />} />
          <Route path="/z/z-from-p" element={<ZFromP />} />
          <Route path="/z/p-from-z" element={<PFromZ />} />

          <Route path="/t" element={<THome />} />
          <Route path="/t/t-from-p" element={<TFromP />} />
          <Route path="/t/p-from-t" element={<PFromT />} />
          <Route path="/t/welchs-df" element={<WelchsDf />} />

          <Route path="/chi-sq" element={<ChiSqHome />} />
          <Route path="/chi-sq/chi-sq-from-p" element={<ChiSqFromP />} />
          <Route path="/chi-sq/p-from-chi-sq" element={<PFromChiSq />} />
        </Routes>
      </Suspense>
    </HashRouter>
  );
}

export default App;
